import React from "react";
import './SimpleMapSidePane.css';
import download from "./download-24.png"
import edit from "./edit-24.png"

class SimpleMapSidePane extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			location: undefined
		};
	}

	setLocation = (location) => {
		this.setState({ location: location });
	}


	render() {


		// this.refSimpleMapSidePane.current.innerHTML = `Clicked on: <br>`

		// 	+ ``;

		if (undefined === this.state.location) {
			return (
				<div>
				</div>
			);
		}
		else {

			let rows = [];

			if (undefined !== this.state.location.Data) {

				for (var i = 0; i < this.state.location.Data.length; i++) {

					const rowID = `row${i}`
					const cell = [];

					let cellID = `cell${i}-Date`; cell.push(<td key={cellID} id={cellID}>{this.state.location.Data[i].Date}</td>)
					cellID = `cell${i}-Cable`; cell.push(<td key={cellID} id={cellID}>{this.state.location.Data[i].Cable}</td>)
					cellID = `cell${i}-Machine`; cell.push(<td key={cellID} id={cellID}>{this.state.location.Data[i].Machine}</td>)
					cellID = `cell${i}-Local`; cell.push(<td key={cellID} id={cellID}>{this.state.location.Data[i].Local}</td>)
					cellID = `cell${i}-Acoustic`; cell.push(<td key={cellID} id={cellID}>{this.state.location.Data[i].Acoustic}</td>)

					rows.push(<tr key={i} id={rowID}>{cell}</tr>)
				}
			}


			return (
				<div className="simple-map-sidebar">
					<img src={process.env.PUBLIC_URL + '/' + this.state.location.mainPhoto} alt="mainPhoto" width="400px" />
					<br></br>

					<div className="simple-map-sidebar-id simple-map-sidebar-text">
						{this.state.location.id}
					</div>

					<div className="simple-map-sidebar-text">
						{this.state.location.Name}
					</div>

					<button><img src={edit} alt="" width="24" />Add/Edit Information</button>
					<button><img src={download} alt="" width="24" />Download Latest Report</button>

					<div className="simple-map-sidebar-description simple-map-sidebar-text">
						{this.state.location.Description}
					</div>

					<img src={process.env.PUBLIC_URL + '/Heatmaps.png'} alt="Heatmaps" width="400px" />

					<table id="simple-board">
						<thead>
							<tr>
								<th>Date</th>
								<th>Cable</th>
								<th>Machine</th>
								<th>Local</th>
								<th>Acoustic</th>
							</tr>
						</thead>
						<tbody>
							{rows}
						</tbody>
					</table>

					<div>
						Photos:
					</div>
				</div>
			);

		}
	};

}

export default SimpleMapSidePane;